.NewsPaper {
    padding: 30px;
}

.NewsPaper iframe {
    width: 100%;
    height: calc( 100vh - 110px );
    overflow: auto;
}

.NewsPaper h2 {
    text-align: center;
    margin-bottom: 20px;
}

@media only screen and (max-width: 992px) {
    .NewsPaper {
        padding: 30px 0;
    }
}